<template>
<div>
    <div class="text-center">
        <div class="date">
            <p class="mr-2">Tháng {{ month }}</p>
            <p>Năm {{ year }}</p>
        </div>

        <p v-if="this.timeMonth > 0">
            Bạn đã học
            <b>
                {{ (this.timeMonth / 60).toFixed(2) }} giờ
                ({{ this.timeMonth }} phút)
            </b>
            trong tháng này
        </p>
        <p v-else>
            Chưa có dữ liệu thống kê cho tháng này
        </p>

        <div>
            <span @click="toBeforeMonth()" class="material-icons btn-icon mr-2">navigate_before</span>

            <input type="month" v-model="date" class="mb-4" style="display: inline-block; position: relative; top: -5px" />

            <span @click="toNextMonth()" class="material-icons btn-icon ml-2">navigate_next</span>
        </div>
            
        <div class="color-help">
            <span
            v-for="(color, index) in styleColors.day"
            :key="index"
            :style="{ backgroundColor: color }"
            ></span>
        </div>

        <div class="color-help">
            <span
            v-for="(color, index) in styleColors.weekday"
            :key="index"
            :style="{ backgroundColor: color }"
            ></span>
        </div>
    </div>

    <div style="max-width: 100%; overflow-x: scroll">
        <table>
            <tr>
                <th title="Tổng thời gian học của tuần">Week</th>
                <th>Sun</th>
                <th>Mon</th>
                <th>Tue</th>
                <th>Wed</th>
                <th>Thu</th>
                <th>Fri</th>
                <th>Sat</th>
            </tr>
            <tr v-for="(row, i) in diaries.data" :key="i">
                <td>
                    <div
                    v-if="diaries.timeWeek[i] != ''">
                        <p :style="weekStyle(diaries.timeWeek[i])">
                            Tuần {{ i + 1 }}
                        </p>
                        <span class="text-secondary">
                            <span
                                style="white-space: nowrap"
                                v-html="caculateTime(
                                    diaries.timeWeek[i],
                                    'h',
                                    '\'',
                                    true
                                )"
                            ></span>
                        </span>
                        <br /><br />
                    </div>
                </td>

                <td v-for="(col, j) in row" :key="j">
                    <p
                    :title="'Ngày ' + col.day"
                    v-if="col.day != ''"
                    @click="setShowDay(i, j, col.day)"
                    :style="dayStyle(col)">
                        {{ col.day }}
                    </p>

                    <div
                    v-if="typeof col.time == 'number'"
                    class="text-secondary mt-1">
                        <span
                            style="white-space: nowrap"
                            v-html="caculateTime(
                                col.time,
                                'h',
                                '\'',
                                true
                            )"
                        ></span>
                        <hr>
                    </div>
                    <div v-else>
                        <br><br><br>
                    </div>
                </td>
            </tr>
        </table>
    </div>

    <div style="margin-bottom: 7rem">
        <h2 class="mb-4">{{ showDay.title }} - {{ showDay.diary.length }} diary</h2>
        <hr>

        <div
        v-if="showDay.diary.length == 0"
        style="margin-bottom: 40vh">
            Bạn không học gì vào ngày này
        </div>
        <div
        v-else
        v-for="diary in showDay.diary"
        :key="'diary_' + diary.id" >
            <div class="ml-3">
                <p v-if="tag == 0">
                    Đã học:
                    <b>{{ caculateTime(diary.time) }}</b>
                        trong môn
                    <b>
                        <router-link :to="'/study-diary/' + diary.sdtag">
                            {{ diary.name }}
                        </router-link>
                    </b>
                </p>
                <p v-else>
                    Đã học:
                    <b>
                        {{ caculateTime(diary.time) }}
                    </b>
                </p>

                <p>Bắt đầu: <b>{{ new Date(diary.start_at).toLocaleString('vi-VN') }}</b></p>
                <p>Kết thúc: <b>{{ new Date(diary.stop_at).toLocaleString('vi-VN') }}</b></p>
                
                <MarkdownViewer
                    class="quote"
                    :content="diary.log"
                />
            </div>
            <br>
            <hr>
            <br>
        </div>
    </div>
</div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import api from '@/store/api'
import MarkdownViewer from '../MarkdownViewer.vue'
import formatTime from '@/js/formatTime'

export default {
    name: 'ContributionDiary',

    components: {
        MarkdownViewer
    },

    props: {
        // User id
        userID: {
            type: Number,
            default: 0
        },

        // Tag id
        tag: {
            type: Number,
            default: 0
        },

        refeshToogle: {
            type: Boolean,
            default: false
        },

        timeToday: {
            type: Number,
            default: 0
        }
    },

    created () {
        this.adjustDate()
    },

    data () {
        return {
            diaries: [],
            showDay: {
                title: 'Hôm nay',
                day: 0,
                diary: []
            },
            month: this.$route.query.month || new Date().getMonth() + 1,
            year: this.$route.query.year || new Date().getFullYear(),
            date: '',
            timeMonth: 0,

            styleColors: {
                day: [
                    '#ffffff',
                    '#ccffed',
                    '#7fffd2',
                    '#2bd89e',
                    '#08a56e',
                    '#007f52',
                    '#00593a',
                    '#003321'
                ],

                weekday: [
                    '#f8efff',
                    '#e9ccff',
                    '#d499ff',
                    '#b14cff',
                    '#801ecc',
                    '#52078c',
                    '#2b004c'
                ]
            }
        }
    },

    computed: {
        ...mapState(['user'])
    },

    methods: {
        ...mapMutations(['setIsLoading']),

        async getDiaries () {
            let url = '/study-diary/diary/'
            let userID = this.userID || this.user.id

            if (this.tag != 0) {
                url += `${userID}/${this.year}/${this.month}/${this.tag}`
            }
            else {
                url += `${userID}/${this.year}/${this.month}`
            }

            if (this.loadSessionData(url)) {
                return
            }
            else {
                this.setIsLoading(true)

                let result = await api.get(url)
                let data = await result.json()

                this.setIsLoading(false)

                if (result.status != 200) {
                    throw new Error(data.error)
                }

                this.showDay.diary = []
                this.putDiary(data.data.diaries, data.data.times)

                this.saveSessionData(url, data.data)
            }
        },

        putDiary (diaries, times) {
            let cols = [
                [], // Sun
                [], // Mon
                [], // Tue
                [], // Wed
                [], // Thu
                [], // Fri
                [] // Sat
            ]

            let rows = {
                timeWeek: [
                    '',
                    '',
                    '',
                    '',
                    '',
                    ''
                ],
                data: [
                    [[],[],[],[],[],[],[]],
                    [[],[],[],[],[],[],[]],
                    [[],[],[],[],[],[],[]],
                    [[],[],[],[],[],[],[]],
                    [[],[],[],[],[],[],[]],
                    cols
                ]
            }

            let date = diaries.length > 0 ? new Date(diaries[0].stop_at) : new Date(this.year, this.month - 1, 1)
            let startDay = new Date(date.getFullYear(), date.getMonth(), 1).getDay()
            let endDay = new Date(date.getFullYear(), date.getMonth() + 1, 0).getDate()
            let count = 1
            
            // Put day number to table
            for (let i = 0; i < rows.data.length; i++) {
                for (let j = 0; j < rows.data[i].length; j++) {
                    let day = count - startDay

                    if (day > 0 && day <= endDay) {
                        let isToday = false;

                        if (this.month == new Date().getMonth() + 1 && this.year == new Date().getFullYear()) {
                            if (new Date().getDate() == day) {
                                isToday = true
                            }
                        }

                        rows.data[i][j] = {
                            day: day,
                            isActive: false,
                            time: null,
                            diary: [],
                            isToday: isToday
                        }
                    }
                    else {
                        rows.data[i][j] = {
                            day: '',
                            diary: []
                        }
                    }

                    count++
                }
            }

            // For setShowDay
            let showDayRow = 0
            let showDayCol = 0
            let showDayDay = new Date().getDate()

            if (diaries) {
                // Put diary to table
                diaries.forEach(diary => {
                    let date = new Date(diary.stop_at)
                    let day = date.getDate()
                    let weekday = date.getDay()

                    let startDay = new Date(date.getFullYear(), date.getMonth(), 1).getDay()

                    let row = Math.ceil((day + startDay) / 7) - 1

                    rows.data[row][weekday].diary.push(diary)

                    if (date.toDateString() == new Date().toDateString()) {
                        showDayRow = row
                        showDayCol = weekday
                        showDayDay = day
                    }
                })
            }

            this.timeMonth = 0
            
            if (times) {
                this.putTimes(rows, times)
            }

            this.diaries = rows
            this.setShowDay(showDayRow, showDayCol, showDayDay)
        },

        putTimes (rows, times) {
                // Put time to table
                times.forEach(time => {
                    let date = new Date(time.created_at)
                    let day = date.getDate()
                    let weekday = date.getDay()

                    let startDay = new Date(date.getFullYear(), date.getMonth(), 1).getDay()

                    let row = Math.ceil((day + startDay) / 7) - 1

                    if (time.type == 0) {
                        rows.data[row][weekday].time = time.time
                    }
                    else if (time.type == 1) {
                        rows.timeWeek[row] = time.time
                    }
                    else if (time.type == 2) {
                        this.timeMonth = time.time
                    }
                })
        },

        caculateTime (minutes, formatHour, formatMinute, isSplitLine) {
            return formatTime(minutes, formatHour, formatMinute, isSplitLine)
        },

        setShowDay (row, col, day) {
            this.showDay.diary = this.diaries.data[row][col].diary
            this.showDay.day = day
            this.showDay.title = `${day}/${this.month}/${this.year}`

            // Reset active style
            for (let i = 0; i < this.diaries.data.length; i++) {
                for (let j = 0; j < this.diaries.data[i].length; j++) {
                    this.diaries.data[i][j].isActive = false
                }
            }

            this.diaries.data[row][col].isActive = true
        },

        dayStyle (col) {
            let style = {
                backgroundColor: '#ffffff',
                outline: col.isActive ? '3px dashed #ff907f' : 'none',
                boxShadow: col.isToday ? '3px 3px 1px #ff90ff' : 'none',
                color: '#000000'
            }

            let index = 0

            if (col.isToday) {
                index = minToColor(this.timeToday)
            }
            else if (typeof col.time == 'number') {
                index = minToColor(col.time)
            }

            style.backgroundColor = this.styleColors.day[index]

            if (index > 4) {
                style.color = '#ffffff'
            }

            return style

            function minToColor (minutes) {
                let hours = Math.floor(minutes / 60)
                let index = 0

                if (minutes <= 0) return index

                if (minutes > 0 && hours < 2) index = 1
                else if (hours < 4) index = 2
                else if (hours < 6) index = 3
                else if (hours < 8) index = 4
                else if (hours < 10) index = 5
                else if (hours < 12) index = 6
                else index = 7
                
                return index
            }
        },

        weekStyle (minutes) {
            let style = {
                backgroundColor: '#fff',
                color: '#000',
                cursor: 'default',
                padding: '.5rem .25rem'
            }

            let hours = Math.floor(minutes / 60)
            let index = 0

            if (hours <= 12) index = 0
            else if (hours <= 24) index = 1
            else if (hours <= 36) index = 2
            else if (hours <= 48) index = 3
            else if (hours <= 60) index = 4
            else if (hours <= 72) index = 5
            else index = 6
            
            style.backgroundColor = this.styleColors.weekday[index]

            if (index > 3) {
                style.color = '#ffffff'
            }

            return style
        },

        saveSessionData (url, data) {
            sessionStorage.setItem(url, JSON.stringify(data))
        },

        loadSessionData (url) {
            let sessionData = sessionStorage.getItem(url)
            if (!sessionData) return false

            let data = JSON.parse(sessionData)

            this.showDay.diary = []
            this.putDiary(data.diaries, data.times)

            return true
        },

        toNextMonth () {
            this.month++

            if (this.month > 12) {
                this.month = 1
                this.year++
            }

            this.adjustDate()
        },

        toBeforeMonth () {
            this.month--

            if (this.month < 1) {
                this.month = 12
                this.year--
            }

            this.adjustDate()
        },

        formatMonth (month) {
            month = String(month)

            if (month.length > 1) return month
            return '0' + month
        },

        adjustDate (month, year) {
            month = month || this.month
            year = year || this.year

            let date = year + '-' + this.formatMonth(month)
            this.date = date
        }
    },

    watch: {
        refeshToogle: {
            handler () {
                this.getDiaries()
            },
            deep: true
        },

        date: {
            handler (value) {
                let data = value.split('-')
                this.year = data[0]
                this.month = data[1]

                this.getDiaries()
            }
        }
    }
}
</script>

