<template>
<div ref="editor">

</div>
</template>

<script>
import '@toast-ui/editor/dist/toastui-editor.css'
import '@toast-ui/editor-plugin-code-syntax-highlight/dist/toastui-editor-plugin-code-syntax-highlight.css'
import 'prismjs/themes/prism.min.css'

import Editor from '@toast-ui/editor'
import codeSyntaxHighlight from '@toast-ui/editor-plugin-code-syntax-highlight'
import Prism from 'prismjs'
import HTMLSanitizer from '@/js/HTMLSanitizer'
import api from '@/store/api'
import { mapMutations } from 'vuex'

export default {
    name: 'MarkdownEditor',

    props: {
        modelValue: {
            type: String,
            required: false,
            default: ''
        }
    },

    emits: ['update:modelValue', 'update:imageHook'],

    mounted () {
        const element = this.$refs.editor

        const editor = new Editor({
            el: element,
            height: '500px',
            hideModeSwitch: true,
            previewHighlight: true,
            customHTMLSanitizer: HTMLSanitizer,
            
            toolbarItems: [
                ['heading', 'bold', 'italic', 'strike'],
                ['hr', 'quote'],
                ['ul', 'ol', 'task'],
                ['table', 'image', 'link'],
                ['code', 'codeblock']
            ],
            
            plugins: [[codeSyntaxHighlight, { highlighter: Prism }]],
            
            events: {
                change: () => this.$emit('update:modelValue', editor.getMarkdown())
            },

            hooks: {
                addImageBlobHook: this.addImageBlobHook
            }
        })
    },

    methods: {
        ...mapMutations(['setIsLoading']),

        async addImageBlobHook (blob, callback) {
            this.setIsLoading(true)

            let formData = new FormData()
            formData.append('image', blob)

            let result = await api.sendFormData('/user/upload-image', formData)
            let data = await result.json()

            this.setIsLoading(false)

            if (result.status !== 200) {
                throw new Error(data.error)
            }

            callback(
                api.host +
                '/user/upload-image/' +
                this.$store.state.user.username +
                '/' +
                data.url
            )
        }
    }
}

</script>