export default function (timeEnd) {
    let dateNow =  new Date()
    let dateEnd = new Date(timeEnd)
    let timeDiff = dateNow - dateEnd

    // Less than 1 minute
    let timeDiffSeconds = Math.floor(timeDiff / 1000)
    if (timeDiffSeconds < 60) return 'Vừa xong'

    // Less than 1 hour
    let timeDiffMinutes = Math.floor(timeDiffSeconds / 60)
    if (timeDiffMinutes < 60) return timeDiffMinutes + ' phút trước'

    // Less than 1 day
    let timeDiffHours = Math.floor(timeDiffMinutes / 60)
    if (timeDiffHours < 24) return timeDiffHours + ' giờ trước'

    // Less than 1 month
    let timeDiffMonths = dateNow.getMonth() - dateEnd.getMonth()
    let timeDiffYears = dateNow.getFullYear() - dateEnd.getFullYear()
    let timeDiffDays = Math.floor(timeDiffHours / 24)

    if (timeDiffDays < 30 || timeDiffMonths == 0)
    return timeDiffDays + ' ngày trước'

    // In the same year
    if (timeDiffYears < 1) return timeDiffMonths + ' tháng trước'
    
    // Different year
    if (timeDiffMonths <= 0) {
        let monthDiff = dateNow.getMonth() + (12 - dateEnd.getMonth())
        return monthDiff + ' tháng trước'
    }
    else {
        return timeDiffYears + ' năm trước'
    }
}